<template>
  <v-card width="500px">
    <v-card-title v-html="'Login'" />
    <v-card-text>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >
        <v-text-field
          v-model="email"
          label="Email"
          dense
          outlined
          :rules="emailRules"
        />
        <v-text-field
          v-model="password"
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show1 ? 'text' : 'password'"
          label="Contraseña"
          dense
          outlined
          @click:append="show1 = !show1"
          :rules="passwordRules"
        />
      </v-form>
      <v-btn class="mb-2" @click="loginEmail" color="accent" block>Ingresar</v-btn>
      <v-btn @click="loginGoogle" color="error" block>ingresar google</v-btn>
      <p class="mt-3">Olvidaste la contraseña? recuperala <a @click="$emit('recovery-password')">aquí</a></p>
      <v-divider class="my-4" />
      <v-btn @click="register()" color="accent" block>Registrarse</v-btn>
    </v-card-text>
  </v-card>
</template>

<script>
import { getAuth, signInWithEmailAndPassword, signInWithPopup, GoogleAuthProvider } from 'firebase/auth'
export default {
  data () {
    return {
      processing: false,
      valid: true,
      email: '',
      show1: false,
      password: '',
      emailRules: [
        v => !!v || 'Email es requerido.',
        v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
      ],
      passwordRules: [
        v => !!v || 'Contraseña es requerido.'
      ]
    }
  },
  methods: {
    async loginEmail () {
      if (this.$refs.form.validate()) {
        const correo = this.email
        const password = this.password
        try {
          const auth = getAuth()
          const userCredentials = await signInWithEmailAndPassword(auth, correo, password)
          if (userCredentials) {
            this.$router.push('/pqrs')
            this.$toasted.success('Inicio sesión con éxito', { icon: 'done', duration: 5000 })
          }
        } catch (error) {
          (error)
          this.$toasted.error('Usuario y/o contraseña errados, o usuario inactivo.', { icon: 'error', duration: 5000 })
        }
      }
    },
    async loginGoogle () {
      const auth = getAuth()
      const provider = new GoogleAuthProvider()
      try {
        await signInWithPopup(auth, provider)
      } catch (error) {
        (error)
      }
    },
    register () {
      this.$emit('change-mode')
    }
  }
}
</script>

<style scoped>
p {
  margin-bottom: 0 !important;
}
</style>
